<template>
<tr>
  <td :colspan="8 - displayArea.cols.length" class="font-weight-bold">
    {{ displayArea.name }}
  </td>
  <td v-for="col in displayArea.cols" :key="col.key" class="text-xs-right">
    {{ col.key }}: {{ col.score }}
  </td>
  <td></td>
  <td class="text-xs-right">{{ displayArea.scoreSum }}</td>
  <td></td>
</tr>
</template>

<script>
import { scoreAreas } from '../common';

export function itemScore(item, availableScores) {
  if (Array.isArray(item)) {
    const reducer = (accumulator, currentValue) => accumulator + availableScores[currentValue];
    return item.reduce(reducer, 0);
  }
  return availableScores[item];
}

export default {
  data() {
    return {
      scoreAreas,
    };
  },
  computed: {
    displayArea() {
      const displayArea = { name: this.area.name, cols: [], scoreSum: 0 };
      const keys = Object.keys(this.area.components);
      keys.forEach((key) => {
        const score = itemScore(this.values[key], this.area.components[key]);
        displayArea.scoreSum += score;
        displayArea.cols.push({ key, score });
      });
      return displayArea;
    },
  },
  props: {
    values: Object,
    area: Object,
    areaKey: String,
  },
};
</script>
